
.product-search {
    width: 80%;
    padding: 10px;
    border-radius: 10px;
    border-width: 0.1em;
    border-style: solid;
    border-color:  #1e8449;
    color:  #1e8449;

}


.product-card-list {
    padding-left: 0;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
}

@media screen and (min-width : 767px) {
    .product-card-list {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        
    }
}